@import url(https://fonts.googleapis.com/css2?family=Quicksand:wght@300;400;500;600;700&display=swap);
/* app text */

body {
  font-family: Roboto, sans-serif;
  font-style: normal;
  font-weight: 300;
  font-size: 16px;
  line-height: 20px;
  letter-spacing: 0.5px;
}


h1 {
  font-family: Roboto, sans-serif;
  font-style: normal;
  font-weight: 300;
  font-size: 4.5em;
  line-height: 82px;
  display: -webkit-flex;
  display: flex;
  -webkit-align-items: center;
          align-items: center;
  letter-spacing: 0.135em;
}


h2 {
  font-family: Roboto, sans-serif;
  font-style: normal;
  font-weight: 300;
  font-size: 2.25em;
  color: #3F4255;
}


/* date-picker */

.date-picker {
  cursor: pointer;
  padding: 26px 24px;
  background: #FFE145;
  border-radius: 4px;
  border: 0;
  color: #B79C10;
  font-family: Roboto, sans-serif;
  font-style: normal;
  font-weight: bold;
  font-size: 1.125em;
  line-height: 1.125em;
  text-align: center;
  letter-spacing: 0.5px;
}

.date-picker:hover {
  padding: 26px 24px;
  background: #FFF7CC;
  border-radius: 4px;
  border: 0;
  color: #B79C10;
  font-family: Roboto, sans-serif;
  font-style: normal;
  font-weight: bold;
  font-size: 1.125em;
  line-height: 1.125em;
  text-align: center;
  letter-spacing: 0.5px;
}



/* world map */

.worldMap {
  float: left;
  background-color: #2B65EC;
  border: 2px solid #000;
}

#mapBackground{
  fill: none;
  pointer-events: all;
}

.country {
  fill: red;
}

.country .active {
  fill: orange;
}

#country-borders {
  fill: none;
  stroke: #fff;
  stroke-width: 1.5px;
  stroke-linejoin: round;
  stroke-linecap: round;
  pointer-events: none;
}


/* tooltip worldmap*/
.hidden {
  display: none;
}

.mapTooltip {
  color: #222; 
  background: #fff; 
  border-radius: 3px; 
  box-shadow: 0px 0px 2px 0px #a6a6a6; 
  padding: .2em; 
  text-shadow: #f5f5f5 0 1px 0;
  opacity: 0.9; 
  position: absolute
}

/* daily informations*/
.countryDailyInformations {
  text-align: left;
}

.countryDailyInformations h2{
  border-bottom: 2px solid #000;
  font-weight:700; 
  border:2px solid #222;padding:5px 11px 3px 11px;
  text-transform:uppercase
}

.countryDailyInformations h2:before {
  background-color: #B79C10;
  content: '';
  display: block;
  height: 3px;
  width: 75px;
  margin-bottom: 5px;
}
.countryDailyInformations h2:after {
  background-color: #B79C10;
  content: '';
  display: block;
  height: 3px;
  width: 75px;
  margin-bottom: 0.25em;
}

/* barchart */
#chart {
  background: #fff;
  width: 100%;
  margin: 1rem auto;
}
.App {
  text-align: center;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    -webkit-animation: App-logo-spin infinite 20s linear;
            animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: column;
          flex-direction: column;
  -webkit-align-items: center;
          align-items: center;
  -webkit-justify-content: center;
          justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@-webkit-keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

@keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

